import React, {useEffect, useState} from 'react'
import {useHistory, useParams} from 'react-router-dom'
import styled from 'styled-components'
//import './Detail.scss'

// Ajax는 1. jQuery Ajax를 쓰든가, 2. axios 설치해서 쓰든가, 3. 쌩자바스크립트 fetch()

const MyBox = styled.div`
  padding : 20px;
  color: green;
`;

const MyTitle = styled.h4`
font-size: 40px;
color: ${props => props.mycolor}
`;

function Detail(props) {

    let [alert, setAlert] = useState(true)
    let [inputValue, setInputValue] = useState('')

    // 컴포넌트가 첫 등장해서 로딩이 끝난 후에 (mount 끝났을 때)
    // 컴포넌트가 재렌더링 되고난 후 때 (update 되고난 후에)
    useEffect(() => {
        console.log('useEffect !')
        let timer = setTimeout(() => {
            setAlert(false)
        }, 2000)

        // 현재 컴포넌트가 사라질 때 (unmount 가 실행될 때)
        return () => {
            clearTimeout(timer)
        }
    }, [alert])


    const {id} = useParams();
    let history = useHistory() // useHistory() 훅 사용

    let product = props.shoes.find((p) => {
        return p.id == id
    })

    return (
        <div className="container">

            <MyBox>
                <MyTitle mycolor={'red'}>Detail</MyTitle>
                <MyTitle mycolor="blue">Detail</MyTitle>
                <MyTitle className="testRed">AAAA</MyTitle>
            </MyBox>

            <input onChange={(e) => {
                setInputValue(e.target.value)
            }}/>

            {
                alert === true
                    ? <div className="my-alert">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>
                    : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}-원</p>
                    <Stock stock={props.stock} setStock={props.setStock}></Stock>
                    <button className="btn btn-danger" onClick={() => {
                        let newStock = [...props.stock]
                        --newStock[0]
                        props.setStock(newStock)
                    }}>주문하기
                    </button>
                    <button className="btn btn-outline-info" onClick={() => {
                        //history.goBack()
                        history.push('/')
                    }}>뒤로 가기
                    </button>
                </div>
            </div>
        </div>
    )
}

function Stock(props) {

    return (
        <p>재고 : {props.stock[0]}</p>
    )
}


export default Detail